.wrapItems {
    padding: 0px;
    margin-top: 45px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.wrapItems li {
    text-decoration: none;
    display: inline-block;
    margin: 0px 15px 15px 0px;
    color: white;
}


.platformItem {
    line-height: 32px;
    padding-left: 15px;
    font-size: 25px;
}

.paginator {
    display: flex;
    list-style: none;
    justify-content: center;
    font-size: 18px;
    color: #ff0000;
}
.paginator .itemLink{
    text-decoration: none;
    padding: 8px 15px;
    color: #181818;
    font-weight: 600;
    font-size: 16px;
}
.paginator .itemLink:hover{
    background: linear-gradient(to right, #ffdf71 35%, #ffd32f 85%);
    border-radius: 100%;
}
.paginator .activeItem {
    background: linear-gradient(to right, #ffdf71 35%, #ffd32f 85%);
    border-radius: 100%;
}

@media (max-width: 650px) {
    .wrapItems li img {
        width: 160px;
        height: 90px;
    }
}
@media(max-width: 1400px) {
    .imgItemStyle {
        width: 210px;
        height: 145px;
    }
}
@media(max-width: 1200px) {
    .imgItemStyle {
        width: 170px;
        height: 145px;
    }

}