.wrapHowToBuy {
    padding-top: 50px;
    text-align: center;
}
.styleForCount {
    background: linear-gradient(to top, #ffd240 35%, #ffd32f 85%);
    color: #131313;
    font-size: 35px;
    border-radius: 100%;
    width: 70px;
    height: 65px;
    line-height: 65px;
    margin: 25px auto;
    font-weight: 600;
    box-shadow: 1px 1px 8px #424242;
}
.imgHowToBuy {
    width: 80%;
    margin: 25px auto;
    border-radius: 15px;
}