.mainImageItem {
    margin: 10px auto 30px;
    display: block;
    height: 300px;
    box-shadow: 1px 1px 8px #696969;
}
.wrapSecondBlock {
    margin: 15px auto;
    width: 320px;
}
.imgItemStyle {
    width: 150px;
    height: 140px;
    box-shadow: 1px 1px 8px #696969;
}
.nameItem {
    margin-top: 15px;
    margin-left: 15px;
    font-weight: 600;
    font-size: 20px;
    display: block;
    text-shadow: 0px 0px 3px #737373;
}
.priceItem {
    font-size: 34px;
    font-weight: 400;
    display: inline-block;
    height: 100px;
    line-height: 120px;
    float: right;
    color: #282c34;

}
.wrapInfoBlock {
    display: inline-block;
    width: 160px;
}
.buttonBuy {
    margin-top: 20px;
    width: 320px;
    height: 55px;
    background: linear-gradient(to right, #ffdf71 35%, #ffd32f 60%, #ffdf71 85%);
    border: none;
    font-size: 30px;
    box-shadow: 1px 1px 8px #696969;
    display: block;
    line-height: 55px;
    color: #282c34 !important;
}
.blockBuyAndPromoCode {
    width: 320px;
}
.buttonBuy:hover {
    box-shadow: 1px 1px 10px #696969;
}
.countBtns {
    display: inline-block;
    margin: 0px 20px 20px 0px
}
.buttonCount {
    display: block;
    width: 45px;
    height: 45px;
    border: none;
    margin: 5px;
    background: #ffd32f;
    font-size: 26px;
    text-align: center;
    line-height: 45px;
    box-shadow: 1px 1px 4px #989898;
    cursor: pointer;
    -moz-user-select: none;
    -khtml-user-select: none;
    user-select: none;
}
.buttonCount:active {
    box-shadow: 1px 1px 6px #8c8c8c;
}
.countWindow {
    width: 125px;
    height: 95px;
    text-align: center;
    font-size: 35px;
    display: inline-block;
    margin-top: 5px;
    position: absolute;
    border: 2px solid #3d3d3d;
    border-radius: 8px;
    box-shadow: 1px 1px 4px #989898;
}
.wrapCountWindow {
    display: inline-block;
}
.countSells {
    margin-top: 15px;
    display: inline-block;
    font-size: 22px;
    background: #ffd32f;
    padding: 0px 15px;
    height: 40px;
    line-height: 40px;
    width: 320px;
    border-radius: 5px;
    text-align: center;
}
.sellerNick {
    margin: 15px 0px;
    display: inline-block;
    font-size: 16px;
    background: #343a40;
    border-radius: 3px;
    padding: 0px 15px;
    color: aliceblue;
    height: 30px;
    line-height: 30px;
}
.descriptionTitle {
    font-size: 28px;
    margin: 25px 0px;
    padding-bottom: 15px;
    border-bottom: 1px solid #8c8c8c;
    width: 100%;
    display: block;
}
delivery {
    word-wrap: break-word;
    padding: 5px 20px;
    display: block;
    background: #c1ffb7;
    border-radius: 8px;
    margin: 5px 0px 10px;
    text-overflow:ellipsis;
}
attention {
    word-wrap: break-word;
    padding: 5px 20px;
    display: block;
    background: #dacf88;
    border-radius: 8px;
    margin: 5px 0px 10px;

}
.review {
    font-size: 18px;
    background: linear-gradient(to top, #ffd240 35%, #ffd32f 85%);
    color: #131313;
    font-weight: 400;
    padding: 10px 8px;
    margin-bottom: 12px;
    border-radius: 8px;
}
.buttonDownloadReviews {
    margin: 25px auto;
    display: block;
    border: 0px;
    width: 210px;
    height: 55px;
    padding: 5px;
    background: #ffd32f;
    color: #131313;
    font-size: 17px;
    font-weight: 600;
    box-shadow: 1px 1px 3px #b7b7b7;
    border-radius: 5px;
}
.wrapImgAndTextReview {
    display: flex;
}
.reviewBlockDate {
    margin-right: 8px;
    float: right;
    color: #444444;
}
.reviewImgWrap {
    display:table;
}
.goodBgReview, .badBgReview {
    font-size: 32px;
    display:table-cell;
    vertical-align:middle;
    padding: 0px 14px 0px 8px;
}
.goodBgReview {
    color: #19c700;
}
.badBgReview {
    color: #ff3535;
}
.wrapReviewItem {
    font-size: 27px;
    margin-bottom: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid #282c34;
}
.countReview {
    font-weight: 600;
}
.thumbGood {
    margin: 0px 5px 0px 10px;
    color: #19c700;
}
.thumbBad {
    margin: 0px 5px 0px 10px;
    color: #ff3535;
}
.reviewWarning {
    margin-left: 50px;
    font-size: 14px;
    display: inline-block;
}
.reviewWarningIcon {
    font-size: 40px;
    display: inline-block;
}
.reviewWarningText {
    padding-left: 14px;
    width: 240px;
    display: inline-block;
}
.notAvailable {
    margin: 90px;
    text-align: center;
    font-size: 22px;
    color: #4f4f4f;
    display: block;
    min-height: 450px;
}
@media (max-width: 500px) {
    .mainImageItem {
        width: 100%;
    }
    .imgItemStyle {
        width: 120px;
        height: 120px;
    }
    .wrapReviewItem {
        font-size: 23px;
    }
}
@media (max-width: 755px) {
    .reviewWarning {
        display: block;
        margin-left: 0px;
    }
}
