.paginator {
    display: flex;
    list-style: none;
    font-size: 18px;
    justify-content: center;
}
.paginatorWrap ul {
    padding: 0px;
    margin: 20px 0px;
}
.paginator .itemLink{
    text-decoration: none;
    padding: 8px 15px;
    color: #181818;
    font-weight: 600;
    font-size: 16px;
}
.paginator .itemLink:hover{
    background: linear-gradient(to right, #ffdf71 35%, #ffd32f 85%);
    border-radius: 100%;
}
.paginator .activeItem {
    background: linear-gradient(to right, #ffdf71 35%, #ffd32f 85%);
    border-radius: 100%;
}