.navLinkStyleLastPurchases {
    display: inline-block;
    transition: box-shadow 185ms;
    box-shadow: 3px 3px 12px #696969;
    margin: 0px 8px 10px 8px;
}
.navLinkStyleLastPurchases:hover {
    box-shadow: 8px 8px 15px #696969;
}
.imgItemStyle {
    width: 230px;
    height: 160px;
}
.panelItem {
    display: block;
    background: linear-gradient(to right, #ffdf71 35%, #ffd32f 85%);
    color: #ffffff;
    height: 32px;
}
.countSell {
    line-height: 25px;
    padding-left: 5px;
    font-size: 15px;
    color: #ffffff;
    text-shadow: 0px 0px 14px rgba(145, 145, 145, 1);
    font-weight: 600;
}
.priceItem {
    float: right;
    padding-right: 15px;
    background: #ffd32f;
    font-size: 15px;
    line-height: 32px;
    font-weight: 600;
}
.titleLastPur {
    text-align: center;

}
.titleLastPurText {
    background: linear-gradient(to right, #ffdf71 35%, #ffd32f 85%);
    padding: 5px 65px;
    font-size: 20px;
    border-radius: 3px;
    color: #181818;
    display: inline-block;
    margin: 16px 0px 35px 0px;
    font-weight: 400;
    box-shadow: 1px 1px 12px #696969;
}
.arrowPrev {
    float: left;
    margin-top: -140px;
    margin-left: -35px;
    width: 100px;
    height: 100px;
    font-size: 35px;
    border: none;
    background: none;

}

.arrowNext {
    float: right;
    margin-top: -140px;
    margin-right: -35px;
    width: 100px;
    height: 100px;
    font-size: 35px;
    border: none;
    background: none;
}

.containerCarousel {
    margin: 0px 50px;
    padding: 0px 0px 11px 0px;
}
.carouselWrap {
    padding-bottom: 30px;
}

@media (max-width: 1023px) {
    .containerCarousel {
        margin: 0px
    }
    .arrowNext, .arrowPrev {
        display: none;
    }
}
@media (max-width: 650px) {
    .titleLastPurText {
        font-size: 18px;
    }
}