.footerWrap {
    background: #131313;
    display: block;
    color: aliceblue;
    border-top: #ffd32f 5px solid;
    padding-top: 15px;
}
.itemFooter {
    display: block;
    width: 220px;
    height: 55px;
    color: #b7b7b7;
    transition: color 200ms;
    background: #343434;
    margin: 12px auto;
    border-radius: 4px;
}
.itemTextFooter {
    text-align: center;
    display: table-cell;
    vertical-align: middle;
    width: 220px;
    height: 55px;
}
.infoFooterItem {
    display: block;
    cursor: pointer;
    color: #b7b7b7;
    margin-bottom: 5px;
    transition: color 200ms;
    text-decoration: none;
}

.footerLogo {
    display: block;
    text-align: center;
    padding-bottom: 35px;
}
.footerInfoUnderLogo {
    font-size: 12px;
    width: 500px;
    display: inline-block;
}
.descriptionInfoFooter {
    display: block;
    text-align: justify;
}
.itemFooter:hover, .infoFooterItem:hover {
    color: #e7e7e7;
}
.paymentWrap {
    display: flex;
    justify-content: center;
    padding-bottom: 35px;
}
.paymentWrap svg {
    margin: 8px 12px;
    font-size: 27px;
}

@media(max-width: 991px) {
    .descriptionInfoFooter {
        padding-bottom: 35px;
    }
    .footerInfoUnderLogo {
        max-width: 300px;
    }
}