.firstTypeSlideBlock {
    display: block;
    width: 100%;
    height: 100%;
    cursor: pointer;
    box-shadow: 3px 3px 12px #696969;
    transition: box-shadow 185ms;
    text-decoration: none;
}
.firstTypeSlideBlock img {
    height: 470px;
}
.secondTypeSlideBlock {
    margin-bottom: 25px;
}
.secondTypeSlideBlock, .secondTypeSlideBlock2 {
    display: block;
    cursor: pointer;
    box-shadow: 3px 3px 12px #696969;
    transition: box-shadow 185ms;
    text-decoration: none;
}
.secondTypeSlideBlock img,
.secondTypeSlideBlock2 img {
    width: 450px;
    height: 204px;
}
.firstTypeSlideBlock:hover, .secondTypeSlideBlock:hover, .secondTypeSlideBlock2:hover {
    box-shadow: 3px 3px 15px #3d3d3d;
}

.panelItem {
    display: block;
    background: linear-gradient(to right, #ffdf71 35%, #ffd32f 85%);
    color: #ffffff;
    height: 36px;
}

.priceItem {
    float: right;
    padding-right: 15px;
    background: #ffd32f;
    font-size: 16px;
    line-height: 36px;
    font-weight: 600;
}
.panelSlideItems {
    padding-top: 50px;
}
.wrapSlider button {
    padding: 0px 25px !important;
    height: 12px !important;
}
.countSellImg {
    padding-left: 15px;
    font-size: 22px;
    line-height: 26px;
}
.countSell {
    font-size: 16px;
    line-height: 36px;
    padding-left: 5px;
    color: #ffffff;
    text-shadow: 0px 0px 14px rgba(145, 145, 145, 1);
    font-weight: 600;
}

@media (max-width: 1370px) {
    .secondTypeSlideBlock img,
    .secondTypeSlideBlock2 img {
        height: 206px;
    }
    .panelItem {
        height: 32px;
    }
    .countSellImg {
        font-size: 20px;
        line-height: 26px;
    }
    .priceItem,
    .countSell {
        font-size: 15px;
        line-height: 30px;
    }
}

@media (max-width: 991px) {
    .secondTypeSlideBlock {
        margin-top: 24px;
    }
}
@media (max-width: 1200px) {
    .firstTypeSlideBlock img {
        height: 320px;
    }
    .secondTypeSlideBlock img,
    .secondTypeSlideBlock2 img {
        height: 131px;
    }
}
@media (max-width: 767px) {
    .firstTypeSlideBlock img {
        height: 250px;
    }
    .secondTypeSlideBlock img,
    .secondTypeSlideBlock2 img {
        height: 120px;
    }
}