.itemLi {
    margin: 8px;
}
.itemLi a {
    text-decoration: none;
}
.navLinkStyle {
    display: inline-block;
    box-shadow: 3px 3px 12px #696969;
    transition: box-shadow 185ms;
}
.navLinkStyle:hover {
    box-shadow: 8px 8px 15px #696969;
}
.imgItemStyle {
    width: 230px;
    height: 160px;
}
.panelItem {
    display: block;
    background: linear-gradient(to right, #ffdf71 35%, #ffd32f 85%);
    color: #ffffff;
    height: 32px;
}

.priceItem {
    float: right;
    padding-right: 15px;
    background: #ffd32f;
    font-size: 15px;
    line-height: 32px;
    font-weight: 600;
}
.countSellImg {
    line-height: 25px;
    padding-left: 15px;
    font-size: 22px;
}
.countSell {
    line-height: 25px;
    padding-left: 5px;
    font-size: 15px;
    color: #ffffff;
    text-shadow: 0px 0px 14px rgba(145, 145, 145, 1);
    font-weight: 600;
}

@media(max-width: 1400px) {
    .imgItemStyle {
        width: 210px;
        height: 145px;
    }
}

@media (max-width: 1200px) {
    .panelItem {
        height: 28px;
    }
    .countSell, .countSellImg {
        line-height: 22px;
        padding-left: 5px;
    }
    .imgItemStyle {
        width: 170px;
        height: 120px;
    }
    .priceItem {
        line-height: 28px;
    }
}
@media (max-width: 650px) {
    .countSell, .countSellImg {
        line-height: 18px;
        padding-left: 5px;
    }
    .panelItem {
        height: 25px;
    }
    .priceItem {
        line-height: 25px;
    }
    .imgItemStyle {
        width: 155px;
        height: 100px;
    }
}