.categoryWrap {
    width: 100%;
    display: flex;
    --bs-gutter-x: 0;
}

.menuItem {
    text-align: center;
    border-bottom: 1px solid #939393;
    color: #4f4f4f;
    height: 50px;
    line-height: 50px;
    font-size: 22px;
    font-weight: 400;
    font-family: inherit;
    transition: box-shadow 185ms;
    cursor: pointer;
}
.menuItem:hover {
    color: #252525;
    border-bottom: 3px solid #dcc25d;
}

.menuItemActive {
    text-align: center;
    color: #252525;
    border-bottom: 3px solid #dcc25d;
    height: 50px;
    line-height: 50px;
    font-size: 22px;
    font-weight: 400;
    font-family: inherit;
    transition: box-shadow 185ms;
    cursor: pointer;
}
.mainWrap {
    padding-top: 25px;
    background: radial-gradient(circle, rgba(247,247,247,1) 0%, rgba(203,203,203,1) 50%, rgba(238,238,238,1) 100%);
}
@media (max-width: 991px) {
    .categoryWrap {
        width: 100%;
        display: block;
    }
    .menuItem {
        margin-bottom: 8px;
    }
    .menuItemActive {
        margin-bottom: 8px;
    }

}