.wrapCatalog {
    display: flex;
}
.wrapFilter {
    display: flex;
    width: 340px;
    padding: 15px 9px 9px 9px;
}
.wrapItemsCatalog {
    display: flex;
    width: 100%;
}
.wrapItemsCatalog, .wrapFilter {
    background: #e3e3e3;
}
.textFilterCatalog {
    font-weight: 400;
    color: #424242;
}
.sortWrapCatalog {
    margin: 18px 0px 0px 20px;
    max-width: 340px;
}
.checkBoxStyle {
    display: flex;
    padding: 2px 0px;
}
.checkBoxTextStyle {
    padding-left: 5px;
    font-size: 17px;
}
.inputPrice {
    width: 70px;
    margin: 15px 5px 0px 5px;
    border-radius: 5px;
    border: 1px solid #6e6e6e;
}
.inputPrice:active {
    border: 1px solid #6e6e6e;;
}
.wrapItems {
    padding: 0px;
    margin-top: 25px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.wrapSecondCatalog {
    margin: 0 auto;
}
.wrapPreloader {
    margin: 50px auto;
}
@media (max-width: 991px) {
    .wrapCatalog {
        display: block;
    }
    .wrapFilter, wrapFilter {
        width: 100%;

    }
}